
.index {
  padding-top: 4rem;
}
.title {
  margin-top: 1rem;
  font-size: 1.8rem;
}
.name {
  position: relative;
  line-height: 40px;
}
.name:after {
  display: block;
  position: absolute;
  left: 0;
  bottom: 0;
  width: 100%;
  border-top: 1px solid rgba(7, 17, 27, 0.1);
  content: '';
}
.name .edit_one {
  position: absolute;
  right: 0px;
  color: #0fae6f;
}
.phone_list {
  width: 80%;
  padding: 1.8rem 10%;
}
.phone_list .list_item {
  box-sizing: border-box;
  float: left;
}
.phone_list .list_item.active .item_texts {
  border: 1px solid #0fae6f;
}
.phone_list .list_item .item_texts {
  border: 1px solid #7e8c8d;
  margin: 1rem 1rem;
  height: 4.6rem;
  line-height: 4.6rem;
  text-align: center;
}
.phone_process {
  border-top: 1px solid #ccc;
}
.phone_process .process_list {
  border-bottom: 1px solid #ccc;
  position: relative;
}
.phone_process .process_list .title {
  font-size: 1.6rem;
  height: 4rem;
  line-height: 4rem;
}
.phone_process .process_list .title span {
  position: absolute;
  right: 10px;
  top: 12px;
  font-size: 2rem;
}
.phone_process .process_list .list_name {
  line-height: 2rem;
}
.fade-enter-active {
  -webkit-transition: all .4s ease;
  transition: all .4s ease;
}
.fade-leave-active {
  opacity: 1;
}
.fade-enter {
  -webkit-transform: translateX(10px);
          transform: translateX(10px);
  opacity: 0;
}
.fade-leave-active {
  opacity: 0;
}



